export default {
	name: 'UniSegmentedControl',
	props: {
		current: {
			type: Number,
			default: 0
		},
		values: {
			type: Array,
			default () {
				return []
			}
		},
		activeColor: {
			type: String,
			default: '#007aff'
		},
		styleType: {
			type: String,
			default: 'button'
		}
	},
	data() {
		return {
			currentIndex: 0
		}
	},
	watch: {
		current(val) {
			if (val !== this.currentIndex) {
				this.currentIndex = val
			}
		}
	},
	created() {
		this.currentIndex = this.current
	},
	methods: {
		_onClick(index) {
			if (this.currentIndex !== index) {
				this.currentIndex = index
				this.$emit('clickItem', index)
			}
		}
	}
}
